.datetimepicker {
  margin-block-start: 10px;
  border-radius: 2px;
  -webkit-box-shadow: 0 0 6px #ccc;
  box-shadow: 0 0 6px #ccc;
  text-align: center;

  &:before {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: middle;
    border-block-end: 7px solid #3bb4f2;
    border-inline-end: 7px solid transparent;
    border-inline-start: 7px solid transparent;
    border-block-start: 0 dotted;
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
    position: absolute;
  }

  &.datetimepicker-rtl {
    direction: rtl;

    table tr td span {
      float: right;
    }
  }

  > div {
    display: none;
  }

  table {
    width: 238px;
    background: #3bb4f2;
    border-radius: 2px;

    th {
      -webkit-transition: background-color 300ms ease-out;
      transition: background-color 300ms ease-out;

      &.next, &.prev {
        -webkit-transition: background-color 300ms ease-out;
        transition: background-color 300ms ease-out;
      }

      width: 34px;
      height: 40px;
      text-align: center;
      font-weight: 400;

      &.switch {
        width: 170px;
        background: #3bb4f2;
        color: #fff;
        cursor: pointer;
      }

      &.next:hover, &.prev:hover, &.switch:hover {
        background: rgba(16, 160, 234, 0.5);
        color: #0084c7;
      }

      &.next, &.prev {
        background: #3bb4f2;
        color: #fff;
        cursor: pointer;
      }

      &.next i, &.prev i {
        width: 34px;
        height: 34px;
        display: inline-block;
      }

      &.next {
        border-start-end-radius: 2px;
      }

      &.prev {
        border-start-start-radius: 2px;
      }

      &.dow {
        color: #3bb4f2;
        background: #fff;
      }

      &.today {
        background: #fff;
        color: #3bb4f2;
        cursor: pointer;

        &:hover {
          background: #f0f0f0;
        }
      }
    }
  }
}

.datetimepicker-days td:hover {
  background: #f0f0f0;
}

.datetimepicker-hours span {
  &.hour:hover, &.minute:hover, &.month:hover, &.year:hover {
    background: #f0f0f0;
  }
}

.datetimepicker-minutes span {
  &.hour:hover, &.minute:hover, &.month:hover, &.year:hover {
    background: #f0f0f0;
  }
}

.datetimepicker-months span {
  &.hour:hover, &.minute:hover, &.month:hover, &.year:hover {
    background: #f0f0f0;
  }
}

.datetimepicker-years span {
  &.hour:hover, &.minute:hover, &.month:hover, &.year:hover {
    background: #f0f0f0;
  }
}

.datetimepicker table {
  .icon-arrow-left:before {
    display: inline-block;
    font: 1.6rem/1 "FontAwesome",sans-serif;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
    content: "\f053";
    vertical-align: bottom;
  }

  .icon-arrow-right:before {
    display: inline-block;
    font: 1.6rem/1 "FontAwesome",sans-serif;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
    content: "\f053";
    vertical-align: bottom;
    content: "\f054";
  }

  td {
    width: 34px;
    height: 34px;
    cursor: pointer;
    font-size: 1.4rem;
    color: #555;
    background: #fff;

    &.today {
      background: rgba(16, 160, 234, 0.5);
      color: #fff;
    }
  }

  span.active, td.active {
    background: #ececec;
    color: #3bb4f2;
  }

  .new, .old {
    color: #89d7ff;
  }

  .disabled {
    background: #fafafa;
    color: #999;
    cursor: not-allowed;
  }
}

.datetimepicker-hours table td, .datetimepicker-minutes table td, .datetimepicker-months table td, .datetimepicker-years table td {
  height: auto;
  background: #fff;
}

.datetimepicker-hours table td {
  &:after, &:before {
    content: " ";
    display: table;
  }
}

.datetimepicker-minutes table td {
  &:after, &:before {
    content: " ";
    display: table;
  }
}

.datetimepicker-months table td {
  &:after, &:before {
    content: " ";
    display: table;
  }
}

.datetimepicker-years table td {
  &:after, &:before {
    content: " ";
    display: table;
  }
}

.datetimepicker-hours table td:after, .datetimepicker-minutes table td:after, .datetimepicker-months table td:after, .datetimepicker-years table td:after {
  clear: both;
}

.datetimepicker-hours span {
  &.hour, &.minute, &.month, &.year {
    float: left;
    height: 38px;
    width: 59.5px;
    line-height: 38px;
  }
}

.datetimepicker-minutes span {
  &.hour, &.minute, &.month, &.year {
    float: left;
    height: 38px;
    width: 59.5px;
    line-height: 38px;
  }
}

.datetimepicker-months span {
  &.hour, &.minute, &.month, &.year {
    float: left;
    height: 38px;
    width: 59.5px;
    line-height: 38px;
  }
}

.datetimepicker-years span {
  &.hour, &.minute, &.month, &.year {
    float: left;
    height: 38px;
    width: 59.5px;
    line-height: 38px;
  }
}

.datetimepicker-hours {
  fieldset, legend {
    margin: 0;
    padding: 0;
  }
}

.datetimepicker-minutes {
  fieldset, legend {
    margin: 0;
    padding: 0;
  }
}

.datetimepicker-inline {
  width: 238px;

  &:before {
    content: none;
  }
}

.datetimepicker-dropdown-bottom-right:before {
  inset-block-start: -7px;
  inset-inline-start: 10px;
}

.datetimepicker-dropdown-bottom-left:before {
  inset-block-start: -7px;
  inset-inline-end: 10px;
}

.datetimepicker-dropdown-top-left, .datetimepicker-dropdown-top-right {
  margin-block-start: 0;
}

.datetimepicker-dropdown-top-left:before, .datetimepicker-dropdown-top-right:before {
  display: inline-block;
  width: 0;
  height: 0;
  vertical-align: middle;
  border-block-start: 7px solid #fff;
  border-inline-end: 7px solid transparent;
  border-inline-start: 7px solid transparent;
  border-block-end: 0 dotted;
  -webkit-transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  transform: rotate(360deg);
  inset-block-end: -7px;
  inset-inline-end: 10px;
}

.am-input-group.date span.add-on {
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  display: none;
}

@media only screen and (max-width: 640px) {
  .datetimepicker {
    width: 100%;
    inset-inline-start: 0 !important;

    table {
      width: 100%;
    }

    td span {
      &.hour, &.minute, &.month, &.year {
        width: 25%;
      }
    }

    &:before {
      content: none;
    }
  }

  .next, .prev {
    width: 44px;
    height: 44px;
  }
}