div {
  &.DTE {
    position: relative;

    div {
      &.DTE_Header {
        position: absolute;
        inset-block-start: 0;
        inset-inline-start: 0;
        height: 52px;
        width: 100%;
        background-color: #e8e9eb;
        border-block-end: 1px solid rgba(0, 0, 0, 0.15);
        padding-block-start: 16px;
    		padding-inline-end: 10px;
    		padding-block-end: 2px;
    		padding-inline-start: 16px;
        font-size: 1.3em;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
      }

      &.DTE_Body {
        position: relative;
      }

      &.DTE_Footer {
        position: absolute;
        inset-block-end: 0;
        inset-inline-start: 0;
        height: 52px;
        width: 100%;
        background-color: #e8e9eb;
        border-block-start: 1px solid rgba(0, 0, 0, 0.15);
        padding: 10px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
      }

      &.DTE_Form_Info {
        margin-block-end: .5em;
        display: none;
      }

      &.DTE_Form_Content {
        position: relative;
        padding: 10px;
      }

      &.DTE_Form_Error {
        float: left;
        padding: 5px;
        display: none;
        color: #b11f1f;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    button.btn, div.DTE_Form_Buttons button {
      position: relative;
      text-align: center;
      display: block;
      margin-block-start: 0;
      padding: .5em 1em;
      cursor: pointer;
      float: right;
      margin-inline-start: .75em;
      color: inherit;
      font-size: 14px;
      white-space: nowrap;
      border: 1px solid #999;
      -webkit-border-radius: 2px;
      -moz-border-radius: 2px;
      -ms-border-radius: 2px;
      -o-border-radius: 2px;
      border-radius: 2px;
      -webkit-box-shadow: 1px 1px 3px #ccc;
      -moz-box-shadow: 1px 1px 3px #ccc;
      box-shadow: 1px 1px 3px #ccc;
      background-color: #dcdcdc;
      background-image: -webkit-linear-gradient(top, #ffffff 0%, #dcdcdc 100%);
      background-image: -moz-linear-gradient(top, #ffffff 0%, #dcdcdc 100%);
      background-image: -ms-linear-gradient(top, #ffffff 0%, #dcdcdc 100%);
      background-image: -o-linear-gradient(top, #ffffff 0%, #dcdcdc 100%);
      background-image: linear-gradient(to bottom, #ffffff 0%, #dcdcdc 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="#ffffff", EndColorStr="#dcdcdc");
    }

    button.btn:hover, div.DTE_Form_Buttons button:hover {
      border: 1px solid #666;
      -webkit-box-shadow: 1px 1px 3px #999;
      -moz-box-shadow: 1px 1px 3px #999;
      box-shadow: 1px 1px 3px #999;
      background-color: #ccc;
      background-image: -webkit-linear-gradient(top, var(--border) 0%, #cccccc 100%);
      background-image: -moz-linear-gradient(top, var(--border) 0%, #cccccc 100%);
      background-image: -ms-linear-gradient(top, var(--border) 0%, #cccccc 100%);
      background-image: -o-linear-gradient(top, var(--border) 0%, #cccccc 100%);
      background-image: linear-gradient(to bottom, var(--border) 0%, #cccccc 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="var(--border)", EndColorStr="#cccccc");
    }

    button.btn:active, div.DTE_Form_Buttons button:active {
      -webkit-box-shadow: inset 1px 1px 3px #999;
      -moz-box-shadow: inset 1px 1px 3px #999;
      box-shadow: inset 1px 1px 3px #999;
    }

    button.btn:focus, div.DTE_Form_Buttons button:focus {
      border: 1px solid #426c9e;
      text-shadow: 0 1px 0 #c4def1;
      background-color: #79ace9;
      background-image: -webkit-linear-gradient(top, #bddef4 0%, #79ace9 100%);
      background-image: -moz-linear-gradient(top, #bddef4 0%, #79ace9 100%);
      background-image: -ms-linear-gradient(top, #bddef4 0%, #79ace9 100%);
      background-image: -o-linear-gradient(top, #bddef4 0%, #79ace9 100%);
      background-image: linear-gradient(to bottom, #bddef4 0%, #79ace9 100%);
      filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="#bddef4", EndColorStr="#79ace9");
      outline: none;
    }

    button.btn:focus:after, div.DTE_Form_Buttons button:focus:after {
      position: absolute;
      inset-block-start: 0;
      inset-inline-start: 0;
      inset-inline-end: 0;
      inset-block-end: 0;
      background: white;
      display: block;
      content: " ";
      -webkit-animation-duration: 1s;
      -webkit-animation-name: buttonPulse;
      -webkit-animation-fill-mode: forwards;
      -webkit-animation-iteration-count: infinite;
      -webkit-animation-timing-function: linear;
      -webkit-animation-direction: alternate;
      -moz-animation-duration: 1s;
      -moz-animation-name: buttonPulse;
      -moz-animation-fill-mode: forwards;
      -moz-animation-iteration-count: infinite;
      -moz-animation-timing-function: linear;
      -moz-animation-direction: alternate;
      -o-animation-duration: 1s;
      -o-animation-name: buttonPulse;
      -o-animation-fill-mode: forwards;
      -o-animation-iteration-count: infinite;
      -o-animation-timing-function: linear;
      -o-animation-direction: alternate;
      animation-duration: 1s;
      animation-name: buttonPulse;
      animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      animation-direction: alternate;
    }

    &.DTE_Action_Remove div.DTE_Body_Content {
      text-align: center;
      padding: 20px 0;
    }
  }

  &.DTTT_container {
    float: left;
  }

  &.DTE_Field {
    input, textarea {
      box-sizing: border-box;
      background-color: white;
      -webkit-transition: background-color ease-in-out .15s;
      transition: background-color ease-in-out 0.15s;
    }

    input:focus, textarea:focus {
      background-color: #ffe;
    }

    input {
      &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
        padding: 5px 4px;
        width: 100%;
        border: 1px solid #aaa;
        border-radius: 3px;
      }

      &[type=checkbox], &[type=radio] {
        vertical-align: middle;
      }
    }

    select {
      padding: 4px;
      border-radius: 3px;
      border: 1px solid #aaa;
      background: white;
    }

    label div.DTE_Label_Info {
      font-size: .85em;
      margin-block-start: 0.25em;

      &:empty {
        margin-block-start: 0;
      }
    }

    div {
      &.DTE_Field_Info, &.DTE_Field_Message, &.DTE_Field_Error {
        font-size: 11px;
        line-height: 1em;
        margin-block-start: 5px;
      }

      &.DTE_Field_Info:empty, &.DTE_Field_Message:empty {
        margin-block-start: 0;
      }

      &.DTE_Field_Error {
        &:empty {
          margin-block-start: 0;
        }

        display: none;
        color: #b11f1f;
      }

      &.multi-value {
        display: none;
        border: 1px dotted #666;
        border-radius: 3px;
        padding: 5px;
        background-color: #fafafa;
        cursor: pointer;

        span {
          font-size: .8em;
          line-height: 1.25em;
          display: block;
          color: #666;
        }

        &.multi-noEdit {
          border: 1px solid #ccc;
          cursor: auto;
          background-color: #fcfcfc;

          &:hover {
            background-color: #fcfcfc;
          }
        }

        &:hover {
          background-color: #f1f1f1;
        }
      }
    }

    &.disabled {
      color: grey;

      div.multi-value {
        cursor: default;
        border: 1px dotted #aaa;
        background-color: transparent;
      }
    }

    div.multi-restore {
      display: none;
      margin-block-start: .5em;
      font-size: .8em;
      line-height: 1.25em;
      color: #3879d9;

      &:hover {
        text-decoration: underline;
        cursor: pointer;
      }
    }
  }

  &.DTE_Field_Type_textarea textarea {
    padding: 3px;
    width: 100%;
    height: 80px;
    border: 1px solid #aaa;
  }

  &.DTE_Field.DTE_Field_Type_date img {
    vertical-align: middle;
    cursor: pointer;
  }

  &.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div {
    margin-block-end: 0.25em;
  }

  &.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div:last-child, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div:last-child {
    margin-block-end: 0;
  }

  &.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div label, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div label {
    margin-inline-start: .75em;
    vertical-align: middle;
  }

  &.DTE_Field_Type_select div.DTE_Field_Input {
    padding-block-start: 4px;
  }

  &.DTE_Field_Type_datatable {
    div {
      &.dataTables_info {
        font-size: .8em;
        line-height: 1.3em;
        padding-inline-end: 1em;
        padding-block-end: 0.5em;

        span.select-info {
          display: block;
        }
      }

      &.dataTables_filter {
        padding-inline-end: 1em;
        padding-block-end: 0.5em;

        input {
          width: 100%;
        }
      }

      &.dt-buttons {
        padding-inline-end: 1em;
        padding-block-end: 0.5em;

        button {
          width: 100%;
        }

        &:empty {
          padding-block-end: 0;
        }
      }

      &.dataTables_paginate {
        float: none;
        text-align: center;
        font-size: 0.8em;
      }
    }

    table.dataTable {
      &.no-footer {
        border-block-end: 1px solid #aaa !important;
      }

      thead {
        th, td {
          font-weight: normal;
          padding-block-start: 3px;
          padding-block-end: 3px;
          border-block-end: 1px solid #aaa;
        }
      }

      tbody {
        th:first-child, td:first-child {
          border-inline-start: 1px solid #aaa;
        }

        th:last-child, td:last-child {
          border-inline-end: 1px solid #aaa;
        }
      }

      tfoot {
        th, td {
          font-weight: normal;
          padding-block-start: 3px;
          padding-block-end: 3px;
          border-block-start: 1px solid #aaa;
        }
      }
    }

    div {
      &.dataTables_scrollHead table.dataTable {
        border-block-end: none !important;

        thead {
          th, td {
            border-block-end: none !important;
          }
        }
      }

      &.dataTables_scrollBody {
        border: 1px solid #aaa !important;
        border-radius: 3px;

        table.dataTable {
          border-block-end: none !important;

          tbody {
            th:first-child, td:first-child {
              border-inline-start: none;
            }

            th:last-child, td:last-child {
              border-inline-end: none;
            }
          }
        }
      }
    }
  }

  &.DTE_Body {
    padding: 52px 0;

    div.DTE_Body_Content {
      position: relative;
      overflow: auto;

      div {
        &.DTE_Form_Info {
          padding-block-start: 1em;
          padding-inline-end: 1em;
          padding-block-end: 0;
          padding-inline-start: 1em;
          margin: 0;
        }

        &.DTE_Field {
          position: relative;
          clear: both;
          padding: 5px 20%;
          border: 1px solid transparent;

          &:after {
            display: block;
            content: ".";
            height: 0;
            line-height: 0;
            clear: both;
            visibility: hidden;
          }

          &:hover {
            background-color: #f9f9f9;
            border: 1px solid #f3f3f3;
          }

          > {
            label {
              float: left;
              width: 40%;
              padding-block-start: 6px;
            }

            div.DTE_Field_Input {
              float: right;
              width: 60%;
            }
          }

          &.full {
            padding-block-start: 5px;
            padding-inline-end: 0;
            padding-block-end: 5px;
            padding-inline-start: 20%;

            > {
              label {
                width: 30%;
              }

              div.DTE_Field_Input {
                width: 70%;
              }
            }
          }

          &.block > div.DTE_Field_Input {
            float: none;
            clear: both;
            width: 100%;
          }
        }
      }
    }
  }
}

@-webkit-keyframes buttonPulse {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.2;
  }
}

@-moz-keyframes buttonPulse {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.2;
  }
}

@-o-keyframes buttonPulse {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.2;
  }
}

@keyframes buttonPulse {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.2;
  }
}

html[dir=rtl] div {
  &.DTE_Body div.DTE_Body_Content div.DTE_Field > {
    label {
      float: right;
    }

    div.DTE_Field_Input {
      float: left;
    }
  }

  &.DTE div.DTE_Form_Buttons button {
    float: left;
  }
}

@media only screen and (max-width: 768px) {
  div.DTE_Body div.DTE_Body_Content div.DTE_Field {
    padding: 5px 10%;

    &.full {
      padding-block-start: 5px;
      padding-inline-end: 0;
      padding-block-end: 5px;
      padding-inline-start: 10%;

      > {
        label {
          width: 35.5%;
        }

        div.DTE_Field_Input {
          width: 64.5%;
        }
      }
    }

    &.block > div.DTE_Field_Input {
      width: 100%;
    }
  }
}

@media only screen and (max-width: 640px) {
  div.DTE_Body div.DTE_Body_Content div.DTE_Field {
    padding: 5px 0;

    &.full {
      padding: 5px 0%;

      > {
        label {
          width: 40%;
        }

        div.DTE_Field_Input {
          width: 60%;
        }
      }
    }

    &.block > div.DTE_Field_Input {
      width: 100%;
    }
  }
}

@media only screen and (max-width: 580px) {
  div.DTE_Body div.DTE_Body_Content div.DTE_Field {
    position: relative;
    clear: both;
    padding: 5px 0;

    > {
      label {
        float: none;
        width: auto;
        padding-block-start: 0;
      }

      div.DTE_Field_Input {
        float: none;
        width: auto;
      }
    }

    &.full, &.block {
      padding: 5px 0;
    }

    &.full > {
      label, div.DTE_Field_Input {
        width: 100%;
      }
    }

    &.block > {
      label, div.DTE_Field_Input {
        width: 100%;
      }
    }
  }
}

div {
  &.DTE_Bubble {
    position: absolute;
    z-index: 11;
    margin-block-start: -6px;
    opacity: 0;

    div {
      &.DTE_Bubble_Liner {
        position: absolute;
        inset-block-end: 0;
        border: 1px solid black;
        width: 300px;
        margin-inline-start: -150px;
        background-color: white;
        box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.5);
        border-radius: 6px;
        border: 1px solid #666;
        padding: 1em;
        background: #fcfcfc;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;

        div {
          &.DTE_Bubble_Table {
            width: 100%;

            > form div.DTE_Form_Content {
              padding: 0;

              div.DTE_Field {
                position: relative;
                margin-block-end: 0.5em;

                &:last-child {
                  margin-block-end: 0;
                }

                > {
                  label {
                    padding-block-start: 0;
                    margin-block-end: 0;
                  }

                  div {
                    padding: 0;

                    input {
                      margin: 0;
                    }
                  }
                }
              }
            }

            div.DTE_Form_Buttons {
              text-align: right;
              margin-block-start: 1em;

              button {
                margin-block-end: 0;
              }
            }
          }

          &.DTE_Header {
            border-start-start-radius: 5px;
            border-start-end-radius: 5px;

            + div {
              &.DTE_Form_Info, &.DTE_Bubble_Table {
                padding-block-start: 42px;
              }
            }
          }

          &.DTE_Form_Error {
            float: none;
            display: none;
            padding: 0;
            margin-block-end: 0.5em;
          }

          &.DTE_Bubble_Close {
            position: absolute;
            inset-block-start: 19px;
            inset-inline-end: 19px;
            cursor: pointer;
            z-index: 12;

            &:after {
              content: "";
              color: rgba(0, 0, 0, 0.5);
              font-weight: bold;
              font-size: 22px;
              font-family: "Courier New", Courier, monospace;
            }

            &:hover:after {
              color: black;
            }
          }
        }
      }

      &.DTE_Bubble_Triangle {
        position: absolute;
        height: 10px;
        width: 10px;
        inset-block-start: -6px;
        background-color: white;
        border: 1px solid #666;
        border-block-start: none;
        border-inline-end: none;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
    }

    &.below div {
      &.DTE_Bubble_Liner {
        inset-block-start: 10px;
        border-inline-start-style: auto;
      }

      &.DTE_Bubble_Triangle {
        inset-block-start: 4px;
        -webkit-transform: rotate(135deg);
        -moz-transform: rotate(135deg);
        -ms-transform: rotate(135deg);
        -o-transform: rotate(135deg);
        transform: rotate(135deg);
      }
    }
  }

  &.DTE_Bubble_Background {
    position: fixed;
    inset-block-start: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    background: -ms-radial-gradient(center, ellipse farthest-corner, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    background: -moz-radial-gradient(center, ellipse farthest-corner, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    background: -o-radial-gradient(center, ellipse farthest-corner, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    background: -webkit-gradient(radial, center center, 0, center center, 497, color-stop(0, rgba(0, 0, 0, 0.3)), color-stop(1, rgba(0, 0, 0, 0.7)));
    background: -webkit-radial-gradient(center, ellipse farthest-corner, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    background: radial-gradient(ellipse farthest-corner at center, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0.7) 100%);
    z-index: 10;

    > div {
      position: absolute;
      inset-block-start: 0;
      inset-inline-end: 0;
      inset-inline-start: 0;
      inset-block-end: 0;
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
      -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000)";

      &:not([dummy]) {
        filter: progid:DXImageTransform.Microsoft.gradient(enabled="false");
      }
    }
  }

  &.DTE_Inline {
    position: relative;
    display: table;
    width: 100%;

    div {
      &.DTE_Inline_Field, &.DTE_Inline_Buttons {
        display: table-cell;
        vertical-align: middle;
      }

      &.DTE_Inline_Field div.DTE_Field, &.DTE_Inline_Buttons div.DTE_Field {
        padding: 0;
      }

      &.DTE_Inline_Field div.DTE_Field > label, &.DTE_Inline_Buttons div.DTE_Field > label {
        display: none;
      }

      &.DTE_Inline_Field div.DTE_Field input {
        &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
          width: 100%;
        }
      }

      &.DTE_Inline_Buttons div.DTE_Field input {
        &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
          width: 100%;
        }
      }

      &.DTE_Inline_Field div.DTE_Form_Buttons button, &.DTE_Inline_Buttons div.DTE_Form_Buttons button {
        margin-block-start: -6px;
    		margin-inline-end: 0;
    		margin-block-end: -6px;
    		margin-inline-start: 4px;
        padding: 5px;
      }

      &.DTE_Field input {
        &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
          margin: -6px 0;
        }
      }

      &.DTE_Field_Error, &.DTE_Form_Error {
        font-size: 11px;
        line-height: 1.2em;
        padding: 0;
        margin-block-start: 10px;
      }

      &.DTE_Field_Error:empty, &.DTE_Form_Error:empty {
        margin-block-start: 0;
      }
    }
  }
}

span.dtr-data div.DTE_Inline {
  display: inline-table;
}

div {
  &.DTED_Lightbox_Wrapper {
    position: fixed;
    inset-block-start: 0;
    inset-inline-start: 50%;
    margin-inline-start: -390px;
    width: 780px;
    height: 100%;
    z-index: 11;

    div {
      &.DTED_Lightbox_Container {
        display: table;
        height: 100%;
        width: 100%;

        div.DTED_Lightbox_Content_Wrapper {
          display: table-cell;
          vertical-align: middle;
          width: 100%;

          div.DTED_Lightbox_Content {
            position: relative;
            box-shadow: 0 12px 30px 0 rgba(0, 0, 0, 0.5);
            border-radius: 6px;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
