div.DTE {
  div {
    &.editor_upload {
      padding-block-start: 4px;

      div {
        &.eu_table {
          display: table;
          width: 100%;
        }

        &.row {
          display: table-row;
        }

        &.cell {
          display: table-cell;
          position: relative;
          width: 50%;
          vertical-align: top;

          + div.cell {
            padding-inline-start: 10px;
          }
        }

        &.row + div.row div.cell {
          padding-block-start: 10px;
        }
      }

      button.btn {
        width: 100%;
        height: 2.3em;
        font-size: .8em;
        text-align: center;
        line-height: 1em;
      }

      input[type=file] {
        width: 100%;
        height: 2.3em;
        font-size: .8em;
        text-align: center;
        line-height: 1em;
        position: absolute;
        inset-block-start: 0;
        inset-inline-start: 0;
        width: 100%;
        opacity: 0;
      }

      div {
        &.drop {
          position: relative;
          box-sizing: border-box;
          width: 100%;
          height: 100%;
          border: 3px dashed #ccc;
          border-radius: 6px;
          min-height: 4em;
          color: #999;
          padding-block-start: 3px;
          text-align: center;

          &.over {
            border: 3px dashed #111;
            color: #111;
          }

          span {
            max-width: 75%;
            font-size: .85em;
            line-height: 1em;
          }
        }

        &.rendered img {
          max-width: 8em;
          margin: 0 auto;
        }
      }

      &.noDrop div {
        &.drop, &.row.second {
          display: none;
        }

        &.rendered {
          margin-block-start: 10px;
        }
      }

      &.noClear div.clearValue button {
        display: none;
      }

      &.multi {
        div {
          &.cell {
            display: block;
            width: 100%;

            div.drop {
              min-height: 0;
              padding-block-end: 5px;
            }
          }

          &.clearValue {
            display: none;
          }
        }

        ul {
          list-style-type: none;
          margin: 0;
          padding: 0;

          li {
            position: relative;
            margin-block-start: 0.5em;

            &:first-child {
              margin-block-start: 0;
            }

            img {
              vertical-align: middle;
            }

            button {
              position: absolute;
              width: 40px;
              inset-inline-end: 0;
              inset-block-start: 50%;
              margin-block-start: -1.5em;
            }
          }
        }
      }
    }

    &.DTE_Processing_Indicator {
      position: absolute;
      inset-block-start: 17px;
      inset-inline-end: 42px;
      height: 2em;
      width: 2em;
      z-index: 20;
      font-size: 12px;
      display: none;
      -webkit-transform: translateZ(0);
      -ms-transform: translateZ(0);
      transform: translateZ(0);
    }
  }

  &.processing div {
    &.DTE_Processing_Indicator {
      display: block;
    }

    &.DTE_Field div.DTE_Processing_Indicator {
      display: none;
    }
  }

  div.DTE_Field div.DTE_Processing_Indicator {
    inset-block-start: 13px;
    inset-inline-end: 0;
    font-size: 8px;
  }

  &.DTE_Inline div.DTE_Processing_Indicator {
    inset-block-start: 5px;
    inset-inline-end: 6px;
    font-size: 6px;
  }

  &.DTE_Bubble div.DTE_Processing_Indicator {
    inset-block-start: 10px;
    inset-inline-end: 14px;
    font-size: 8px;
  }

  div.DTE_Processing_Indicator {
    span, &:before, &:after {
      display: block;
      background: black;
      width: .5em;
      height: 1.5em;
      border: 1px solid rgba(0, 0, 0, 0.4);
      background-color: rgba(0, 0, 0, 0.1);
      -webkit-animation: editorProcessing .9s infinite ease-in-out;
      animation: editorProcessing 0.9s infinite ease-in-out;
    }

    &:before, &:after {
      position: absolute;
      inset-block-start: 0;
      content: "";
    }

    &:before {
      inset-inline-start: -1em;
      -webkit-animation-delay: -0.3s;
      animation-delay: -0.3s;
    }

    span {
      -webkit-animation-delay: -0.15s;
      animation-delay: -0.15s;
    }

    &:after {
      inset-inline-start: 1em;
    }
  }
}

@-webkit-keyframes editorProcessing {
  0%, 80%, 100% {
    transform: scale(1, 1);
  }

  40% {
    transform: scale(1, 1.5);
  }
}

@keyframes editorProcessing {
  0%, 80%, 100% {
    transform: scale(1, 1);
  }

  40% {
    transform: scale(1, 1.5);
  }
}