#ui_notifIt {
  position: fixed;
  inset-block-start: 10px;
  inset-inline-end: 10px;
  cursor: pointer;
  overflow: hidden;
  -webkit-box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.3);
  -o-box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.3);
  -wekbit-border-radius: 5px;
  -moz-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
  z-index: 20000;

  &:hover {
    opacity: 1 !important;
  }

  p {
    text-align: center;
    font-size: 14px;
    padding: 0;
    margin: 0;
    padding: 0px;
    font-weight: 400;
    text-transform: capitalize;

    i {
      font-size: 20px;
    }
  }
}

#notifIt_close {
  position: absolute;
  color: #FFF;
  inset-block-start: 0;
  padding: 0px 5px;
  inset-inline-end: 0;

  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

/* Color setup */
/* You are free to change all of this */

#ui_notifIt {
  &.success {
    background-color: #03c895;
    color: white;
  }

  &.error {
    background-color: #ff473d;
    color: white;
  }

  &.warning {
    background-color: #ffc107;
    color: white;
  }

  &.info {
    background-color: #01b8ff;
    color: white;
  }

  &.default {
    background-color: #7987a1;
    color: #fff;
  }

  &.primary {
    background-color: $primary;
    color: #fff;
  }

  &.dark {
    background-color: #000;
    color: #fff;
  }
}

/* notifit confirm */

.notifit_confirm_bg, .notifit_prompt_bg {
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

.notifit_confirm, .notifit_prompt {
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  padding: 30px;
  background-color: rgb(var(--white));
  border: 0;
  border-radius: 3px;
  -webkit-box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgb(162, 172, 202);
  z-index: 9999;
}

.notifit_confirm_accept, .notifit_confirm_cancel, .notifit_prompt_accept, .notifit_prompt_cancel {
  display: inline-block;
  font-weight: 400;
  color: #160248;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 4px;
  margin-inline-end: 5px;
  outline: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.notifit_prompt_accept:hover, .notifit_prompt_cancel:hover {
  background-color: #666;
  outline: none;
}

.notifit_confirm_accept:focus, .notifit_confirm_cancel:focus {
  outline: none;
}

.notifit_confirm_accept {
  background-color: #19b159;
  color: #fff;
}

.notifit_confirm_cancel {
  background-color: #fd6074;
  color: #fff;
}

.notifit_confirm_message {
  text-align: center;
  margin-block-end: 20px;
  font-size: 14px;
}

.notifit_prompt_message {
  color: #444;
  margin-block-start: 0;
  text-align: center;
}

.notifit_prompt_input {
  text-align: center;
  font-size: 14px;
  width: 100%;
  padding: 10px;
  outline: none;
  border: 1px solid #AAA;
  color: #444;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
}

.notifit_prompt {
  text-align: center;
}

#ui_notifIt.info {
  margin: 0 auto;
  inset-inline-end: 10px !important;
  inset-inline-start: 10px !important;
}