  padding: 0;
  vertical-align: middle;
}

.ui-spinner-input {
  border: none;
  background: none;
  color: inherit;
  padding: .222em 0;
  margin: .2em 0;
  vertical-align: middle;
  margin-inline-start: .4em;
  margin-inline-end: 2em;
}

.ui-spinner-button {
  width: 1.6em;
  height: 50%;
  font-size: .5em;
  padding: 0;
  margin: 0;
  text-align: center;
  position: absolute;
  cursor: default;
  display: block;
  overflow: hidden;
  inset-inline-end: 0;
}

/* more specificity required here to override default borders */

.ui-spinner a.ui-spinner-button {
  border-block-start-style: none;
  border-block-end-style: none;
  border-inline-end-style: none;
}

.ui-spinner-up {
  inset-block-start: 0;
}

.ui-spinner-down {
  inset-block-end: 0;
}

.ui-tabs {
  position: relative;

  /* position: relative prevents IE scroll bug (element with position: relative inside container with overflow: auto appear as "fixed") */
  padding: .2em;

  .ui-tabs-nav {
    margin: 0;
    padding-block-start: .2em;
    padding-inline-end: .2em;
    padding-block-end: 0;
    padding-inline-start: .2em;
    padding-block-start: .2em;
    padding-inline-end: .2em;
    padding-block-end: 0;
    padding-inline-start: .2em;

    li {
      list-style: none;
      float: left;
      position: relative;
      inset-block-start: 0;
      margin-block-start: 1px;
      margin-inline-end: .2em;
      margin-block-end: 0;
      margin-inline-start: 0;
      border-block-end-width: 0;
      padding: 0;
      white-space: nowrap;
    }

    .ui-tabs-anchor {
      float: left;
      padding: .5em 1em;
      text-decoration: none;
    }

    li {
      &.ui-tabs-active {
        margin-block-end: -1px;
        padding-block-end: 1px;

        .ui-tabs-anchor {
          cursor: text;
        }
      }

      &.ui-state-disabled .ui-tabs-anchor, &.ui-tabs-loading .ui-tabs-anchor {
        cursor: text;
      }
    }
  }
}

.ui-tabs-collapsible .ui-tabs-nav li.ui-tabs-active .ui-tabs-anchor {
  cursor: pointer;
}

.ui-tabs .ui-tabs-panel {
  display: block;
  border-width: 0;
  padding: 1em 1.4em;
  background: none;
}

.ui-tooltip {
  padding: 8px;
  position: absolute;
  z-index: 9999;
  max-width: 300px;
}

body .ui-tooltip {
  border-width: 2px;
}

/* Component containers
----------------------------------*/

.ui-widget {
  font-family: Arial,Helvetica,sans-serif;
  font-size: 1em;

  .ui-widget {
    font-size: 1em;
  }

  input, select, textarea, button {
    font-family: Arial,Helvetica,sans-serif;
    font-size: 1em;
  }

  &.ui-widget-content {
    border: 1px solid #c5c5c5;
  }
}

.ui-widget-content {
  border: 1px solid $border !important;
  background: $background;
  color: $color;

  a {
    color: #333333;
  }
}

.ui-widget-header {
  border: 1px solid #dddddd;
  background: #e9e9e9;
  color: #333333;
  font-weight: bold;

  a {
    color: #333333;
  }
}

/* Interaction states
----------------------------------*/

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button {
  border: 1px solid #c5c5c5;
  background: #f6f6f6;
  font-weight: normal;
  color: #454545;
}

html .ui-button.ui-state-disabled {
  &:hover, &:active {
    border: 1px solid #c5c5c5;
    background: #f6f6f6;
    font-weight: normal;
    color: #454545;
  }
}

.ui-state-default a {
  color: #454545;
  text-decoration: none;

  &:link, &:visited {
    color: #454545;
    text-decoration: none;
  }
}

a {
  &.ui-button, &:link.ui-button, &:visited.ui-button {
    color: #454545;
    text-decoration: none;
  }
}

.ui-button {
  color: #454545;
  text-decoration: none;
}

.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus {
  border: 1px solid #cccccc;
  background: #ededed;
  font-weight: normal;
  color: #2b2b2b;
}

.ui-button {
  &:hover, &:focus {
    border: 1px solid #cccccc;
    background: #ededed;
    font-weight: normal;
    color: #2b2b2b;
  }
}

.ui-state-hover a {
  color: #2b2b2b;
  text-decoration: none;

  &:hover, &:link, &:visited {
    color: #2b2b2b;
    text-decoration: none;
  }
}

.ui-state-focus a {
  color: #2b2b2b;
  text-decoration: none;

  &:hover, &:link, &:visited {
    color: #2b2b2b;
    text-decoration: none;
  }
}

a.ui-button {
  &:hover, &:focus {
    color: #2b2b2b;
    text-decoration: none;
  }
}

.ui-visual-focus {
  box-shadow: 0 0 3px 1px rgb(94, 158, 214);
}

.ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active, a.ui-button:active {
  border: 1px solid #003eff;
  background: $primary;
  font-weight: normal;
  color: #ffffff;
}

.ui-button {
  &:active, &.ui-state-active:hover {
    border: 1px solid #003eff;
    background: $primary;
    font-weight: normal;
    color: #ffffff;
  }
}

.ui-icon-background {
  border: #003eff;
  background-color: #ffffff;
}

.ui-state-active {
  .ui-icon-background {
    border: #003eff;
    background-color: #ffffff;
  }

  a {
    color: #ffffff;
    text-decoration: none;

    &:link, &:visited {
      color: #ffffff;
      text-decoration: none;
    }
  }
}

/* Interaction Cues
----------------------------------*/

.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight {
  border: 1px solid #dad55e;
  background: #fffa90;
  color: #777620;
}

.ui-state-checked {
  border: 1px solid #dad55e;
  background: #fffa90;
}

.ui-state-highlight a, .ui-widget-content .ui-state-highlight a, .ui-widget-header .ui-state-highlight a {
  color: #777620;
}

.ui-state-error, .ui-widget-content .ui-state-error, .ui-widget-header .ui-state-error {
