.SumoSelect {
  p {
    margin: 0;
  }

  width: 200px;
}

.SelectBox {
  padding: 5px 8px;
}

.sumoStopScroll {
  overflow: hidden;
}

/* Filtering style */

.SumoSelect {
  .hidden {
    display: none;
  }

  .search-txt {
    display: none;
    outline: none;
  }

  .no-match {
    display: none;
    padding: 6px;
  }

  &.open {
    .search-txt {
      display: inline-block;
      position: absolute;
      inset-block-start: 0;
      inset-inline-start: 0;
      width: 100%;
      margin: 0;
      padding: 5px 8px;
      border: none;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      border-radius: 5px;
    }

    > .search > {
      span, label {
        visibility: hidden;
      }
    }
  }
}

/*this is applied on that hidden select. DO NOT USE display:none; or visiblity:hidden; and Do not override any of these properties. */

.SelectClass, .SumoUnder {
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  inset-inline-end: 0;
  height: 100%;
  width: 100%;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity = 0);
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
}

.SelectClass {
  z-index: 1;
}

.SumoSelect {
  > {
    .optWrapper > .options li.opt label, .CaptionCont {
      user-select: none;
      -o-user-select: none;
      -moz-user-select: none;
      -khtml-user-select: none;
      -webkit-user-select: none;
    }
  }

  .select-all > label {
    user-select: none;
    -o-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
  }

  display: inline-block;
  position: relative;
  outline: none;
  width: 100%;

  &:focus > .CaptionCont {
    border-color: #99a6b7;
  }

  > {
    .CaptionCont {
      position: relative;
      border: 1px solid $border;
      color: #99a6b7;
      min-height: 14px;
      background-color: rgb(var(--white));
      border-radius: 3px;
      margin: 0;

      > {
        span {
          display: block;
          padding-inline-end: 30px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          cursor: default;

          &.placeholder {
            color: $color;
          }
        }

        label {
          position: absolute;
          inset-block-start: 0;
          inset-inline-end: 0;
          inset-block-end: 0;
          width: 30px;

          > i:before {
            display: inline-block;
            width: 0;
            height: 0;
            margin-inline-start: .255em;
            vertical-align: .255em;
            content: "";
            border-block-start: .3em solid;
            border-inline-end: .3em solid transparent;
            border-block-end: 0;
            border-inline-start: .3em solid transparent;
            margin-block-start: 13px;
          }
        }
      }
    }

    .optWrapper {
      display: none;
      z-index: 9;
      inset-block-start: 30px;
      width: 100%;
      position: absolute;
      inset-inline-start: 0;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      background: rgb(var(--white));
      overflow: hidden;
    }
  }

  &.open > .optWrapper {
    inset-block-start: 35px;
    display: block;
    box-shadow: 0 2px 5px 0 rgba(162, 169, 204, 0.24);

    &.up {
      inset-block-start: auto;
      inset-block-end: 100%;
      margin-block-end: 5px;
    }
  }

  > .optWrapper {
    ul {
      list-style: none;
      display: block;
      padding: 0;
      margin: 0;
      overflow: auto;
    }

    > .options {
      border-radius: 2px;
      position: relative;

      /*Set the height of pop up here (only for desktop mode)*/
      max-height: 250px;

      /*height*/
    }

    &.okCancelInMulti > .options {
      border-start-start-radius: 2px;
      border-start-end-radius: 2px;
      border-end-end-radius: 0;
      border-end-start-radius: 0;
    }

    &.selall {
      > .options {
        border-start-start-radius: 0;
        border-start-end-radius: 0;
        border-end-end-radius: 2px;
        border-end-start-radius: 2px;
      }

      &.okCancelInMulti > .options {
        border-radius: 0;
      }
    }

    > .options li {
      &.group.disabled > label {
        opacity: 0.5;
      }

      ul li.opt {
        padding-inline-start: 22px;
      }
    }

    &.multiple > .options li ul li.opt {
      padding-inline-start: 50px;
    }

    &.isFloating > .options {
      max-height: 100%;
    }

    > .options {
      li.opt {
        padding: 6px 6px;
        position: relative;
        border-block-end: 1px solid $border;
      }

      > li.opt:first-child {
        border-start-start-radius: 2px;
        border-start-end-radius: 2px;
        border-end-end-radius: 0;
        border-end-start-radius: 0;
      }
    }

    &.selall > .options > li.opt:first-child {
      border-radius: 0;
    }

    > .options > li.opt:last-child {
      border-start-start-radius: 0;
      border-start-end-radius: 0;
      border-end-end-radius: 2px;
      border-end-start-radius: 2px;
      border-block-end: none;
    }

    &.okCancelInMulti > .options > li.opt:last-child {
      border-radius: 0;
    }

    > .options li.opt {
      &:hover {
        background-color: $background;
      }

      &.sel {
        background-color: #a1c0e4;
      }
    }
  }

  .select-all {
    &.sel {
      background-color: #a1c0e4;
    }

    > span {
      position: absolute;
      display: block;
      width: 30px;
      inset-block-start: 0;
      inset-block-end: 0;
      margin-inline-start: -35px;
    }

    > span i {
      position: absolute;
      margin: auto;
      inset-inline-start: 0;
      inset-inline-end: 0;
      inset-block-start: 0;
      inset-block-end: 0;
      width: 14px;
      height: 14px;
      border: 1px solid $border;
      border-radius: 2px;
      background-color: #fff;
    }

    &.selected > span i, &.partial > span i {
      background-color: $primary;
      box-shadow: none;
      border-color: transparent;
      background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAGCAYAAAD+Bd/7AAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAALEgAACxIB0t1+/AAAABx0RVh0U29mdHdhcmUAQWRvYmUgRmlyZXdvcmtzIENTNXG14zYAAABMSURBVAiZfc0xDkAAFIPhd2Kr1WRjcAExuIgzGUTIZ/AkImjSofnbNBAfHvzAHjOKNzhiQ42IDFXCDivaaxAJd0xYshT3QqBxqnxeHvhunpu23xnmAAAAAElFTkSuQmCC');
      background-repeat: no-repeat;
      background-position: center center;
    }
  }

  > .optWrapper {
    > .options li {
      label {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        display: block;
        cursor: pointer;
        margin-block-end: 0 !important;
      }

      span {
        display: none;
      }

      &.group > label {
        cursor: default;
        padding: 8px 6px;
        font-weight: bold;
      }
    }

    &.isFloating {
      position: fixed;
      inset-block-start: 0;
      inset-inline-start: 0;
      inset-inline-end: 0;
      width: 90%;
      inset-block-end: 0;
      margin: auto;
      max-height: 90%;
    }

    > .options li.opt.disabled {
      background-color: inherit;
      pointer-events: none;

      * {
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";

        /* IE 5-7 */
        filter: alpha(opacity = 50);

        /* Netscape */
        -moz-opacity: 0.5;

        /* Safari 1.x */
        -khtml-opacity: 0.5;

        /* Good browsers */
        opacity: 0.5;
      }
    }

    &.multiple > .options li.opt {
      padding-inline-start: 35px;
      cursor: pointer;

      span {
        position: absolute;
        display: block;
        width: 30px;
        inset-block-start: 0;
        inset-block-end: 0;
        margin-inline-start: -35px;
      }
    }
  }

  > .optWrapper.multiple > .options li.opt span i {
    position: absolute;
    margin: auto;
    inset-inline-start: 0;
    inset-inline-end: 0;
    inset-block-start: 0;
    inset-block-end: 0;
    width: 14px;
    height: 14px;
    border: 1px solid $border;
    border-radius: 2px;
    background-color: rgb(var(--white));
  }

  > .optWrapper {
    > .MultiControls {
      display: none;
      border-block-start: 1px solid $border;
      background-color: $background;
      border-start-start-radius: 0;
      border-start-end-radius: 0;
      border-end-end-radius: 3px;
      border-end-start-radius: 3px;
    }

    &.multiple {
      &.isFloating > .MultiControls {
        display: block;
        margin-block-start: 5px;
        position: absolute;
        inset-block-end: 0;
        width: 100%;
      }

      &.okCancelInMulti > .MultiControls {
        display: block;

        > p {
          padding: 6px;

          &:focus {
            border-color: $primary;
            outline: none;
            background-color: $primary;
          }
        }
      }

      > .MultiControls > p {
        display: inline-block;
        cursor: pointer;
        padding: 12px;
        width: 50%;
        box-sizing: border-box;
        text-align: center;

        &:hover {
          background-color: $background;
        }

        &.btnOk {
          border-inline-end: 1px solid $border;
          border-start-start-radius: 0;
          border-start-end-radius: 0;
          border-end-end-radius: 0;
          border-end-start-radius: 3px;
        }

        &.btnCancel {
          border-start-start-radius: 0;
          border-start-end-radius: 0;
          border-end-end-radius: 3px;
          border-end-start-radius: 0;
        }
      }
    }

    &.isFloating > .options li.opt {
      padding: 12px 6px;
    }

    &.multiple {
      &.isFloating {
        > .options li.opt {
          padding-inline-start: 35px;
        }

        padding-block-end: 43px;
      }

      > .options li.opt.selected span i {
        background-color: $primary;
        box-shadow: none;
        border-color: transparent;
        background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAGCAYAAAD+Bd/7AAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAALEgAACxIB0t1+/AAAABx0RVh0U29mdHdhcmUAQWRvYmUgRmlyZXdvcmtzIENTNXG14zYAAABMSURBVAiZfc0xDkAAFIPhd2Kr1WRjcAExuIgzGUTIZ/AkImjSofnbNBAfHvzAHjOKNzhiQ42IDFXCDivaaxAJd0xYshT3QqBxqnxeHvhunpu23xnmAAAAAElFTkSuQmCC');
        background-repeat: no-repeat;
        background-position: center center;
      }
    }
  }

  &.disabled {
    opacity: 0.7;
    cursor: not-allowed;

    > .CaptionCont {
      border-color: $border;
      box-shadow: none;
    }
  }

  .select-all {
    border-start-start-radius: 3px;
    border-start-end-radius: 3px;
    border-end-end-radius: 0;
    border-end-start-radius: 0;
    position: relative;
    border-block-end: 1px solid $border;
    background-color: rgb(var(--white));
    padding-block-start: 8px;
    padding-inline-end: 0;
    padding-block-end: 3px;
    padding-inline-start: 35px;
    height: 36px;
    cursor: pointer;

    > {
      label, span i {
        cursor: pointer;
      }
    }

    &.partial > span i {
      background-color: $border;
    }
  }

  > .optWrapper > .options li.optGroup {
    padding-inline-start: 5px;
    text-decoration: underline;
  }
}
